import React,{Component} from 'react'
import { Doughnut, Line, Bar, Bubble,Pie} from 'react-chartjs-2';
import Drivers from '../../components/Drivers'

export default class Accepted extends React.Component{
    constructor (props){
        super(props)
        this.state = {
            data:[],
            driversArr: [],
            total : 0
        }
    }

    componentDidMount(){
        this.getDrivers()
    }

    getDrivers(){
        if (this.Drivers.state.loading == false){
          this.setState({
          driversArr : this.Drivers.state.accepted,
        //   total :  this.Drivers.state.accepted.length
        })
        localStorage.setItem("totalAccepted", 20)
        console.log(localStorage.getItem("totalAccepted"))
        this.countDrivers(this.Drivers.state.accepted)
        setTimeout(() => {
          this.getDrivers();
        }, 60000);
        }else{
          setTimeout(() => {
            this.getDrivers();
          }, 1000);
        }
      }

      countDrivers(drivers){
        var tempArr =  new Array()
          for (var i = 0; i < this.state.months.length; i++){
              var total = 0;
              for (var x = 0; x < drivers.length; x++){
                  if (this.state.months[i] == drivers[x]){
                    total++;
                  }
              }
              tempArr.push(total)
          }
          this.setState({
            data:{
                labels: this.state.months,
                datasets: [{
                    backgroundColor: 'rgb(191, 86, 151)',
                    borderColor: 'rgb(255, 99, 132)',
                    data: tempArr,
                }],
                options: { 
                    scales: {
                        yAxes: [{
                            stacked: true
                        }]
                    }
                }
            }
          })
      }

    render(){
        return(
                <div>
                    <div style={{display:'none'}}>
                        <Drivers ref={ref=>{this.Drivers = ref}} />
                    </div>
                    <Doughnut data={this.state.data} legend={false} />
                </div>
        )
    }
}